<template>
  <div id="app">
    <div class="top">
      <div class="picture-count">
        本相册共有<span class="count">{{ photoList.length }}</span>张照片
      </div>
      <button class="download" @click="downLoad">下载所有照片</button>
    </div>
    <div class="container">
      <img v-for="(item,index) in photoList" :key="index" :src="item" alt="">
    </div>

    <!-- 下载中 -->
    <div class="loading" v-if="loading">
        <div class="torus-box">
          <div class="torus"></div>
          <div class="torus-text">
            正在下载中，请耐心等待...
          </div>
        </div>
    </div>

  </div>
</template>

<script>
import JSZip from 'jszip'
import FileSaver from "file-saver"
import axios from 'axios'

export default {
  name: 'App',
  data () {
    return {
      count: 0,
      photoList:[],
      loading: false,
    }
  },
  created(){

  },
  mounted(){
    this.getPhotoList();
  },
  methods:{
    downJson(url) {
      const that = this
      fetch(url)
      .then(function (response) {
        return response.text();
      })
      .then(function (text) {
        // 解析json数据
        const startIndex = text.indexOf('(');
        const endIndex = text.lastIndexOf(')');
        const json = JSON.parse(text.substring(startIndex + 1, endIndex));

        that.photoList = json.data.photoList.map(item => item.url);
      });
    },
    getPhotoList(){
      this.photoList = [
        "https://img0.baidu.com/it/u=4162443464,2854908495&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500",
        "https://img1.baidu.com/it/u=1960110688,1786190632&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=281",
        "https://img1.baidu.com/it/u=413643897,2296924942&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500",
        "https://img0.baidu.com/it/u=4162443464,2854908495&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500",
      ]
      // const url = localStorage.getItem("qzoneurl");
      // if(url){
      //   const newUrl = url.replace(/(pageStart=)\d+/, 'pageStart=0').replace(/(pageNum=)\d+/, 'pageNum=10000');  
      //   this.downJson(newUrl);
      // }else{
      //   alert("没有获取到相册地址");
      // }
    },
    // 多张调用方法
    getFile(url) {
      return new Promise((resolve, reject) => {
        axios({
          method: 'get',
          url: url,
          responseType: 'blob'
        }).then(response => {
          resolve(response.data)
        }).catch(error => {
          reject(error.toString())
        })
      })
    },
    downLoad() {
      let photoList = this.photoList || [];
      if (photoList.length <= 0) {
        alert("无可导出图片");
      } else {
        this.loading = true;
        let zip = new JSZip()
        let promises = []
        let cache = {}
        let promise
        for (let i = 0; i < photoList.length; i++) {
          promise = this.getFile(photoList[i]).then(data => {
            // 下载文件, 并存成ArrayBuffer对象
            zip.file(`${i+1}.jpg`, data, { binary: true })
            cache[i] = data
          })
          promises.push(promise)
        }
        console.log(promises);

        Promise.all(promises).then(() => {
          zip.generateAsync({ type: 'blob' }).then(res => {
            // 生成二进制流
            FileSaver.saveAs(res, "相册图片.zip")
            // 利用file-saver保存文件  自定义文件名
            this.loading = false;
          })
        }).catch(error => {
          console.log(error);
          this.loading = false;
          alert("文件下载失败，请刷新页面并按F12,然后重新下载");
        })
      }
    },

  }
}
</script>

